<template>
    <div>
        <div class="loading" v-if="load">
            <mt-spinner type="double-bounce" color="#26a2ff" :size="60"></mt-spinner>
        </div>
        <mt-header fixed title="">
            <router-link to="/login" slot="left">
                <mt-button icon="back"></mt-button>
            </router-link>
        </mt-header>
        <div class="main">
            <div class="members">
                <img src="../assets/logo.png" alt="">
                <mt-field label="" placeholder="" state="" v-model="nickname"></mt-field>
                <mt-field label="" placeholder="" state="" type="tel" v-model="mobile"></mt-field>
                <mt-field label="" placeholder="" state="" type="password" v-model="password"></mt-field>
                <mt-button type="primary" size="large" @click="onSubmit" plain></mt-button>
            </div>
        </div>
    </div>
</template>

<script>
import cookie from '../assets/js/cookie';
import { register } from '../api/api'
import { getUserDetail } from '../api/api'
import { MessageBox } from 'mint-ui'

export default {
    name: 'reg',
    components: {
        
    },
    data(){
        return{
            load:true,
            nickname:'',
            mobile:'',
            password:''
        }
    },
    methods:{
        onSubmit(){
            let _this = this
            _this.load = true
            let k_l = (_this.nickname).length
            let n_l = _this.mobile
            n_l = n_l.toString()
            let p_l = (_this.password).length
            if(k_l > 10 || k_l < 1){ // 
                _this.load = false
                MessageBox('', '110');
            }else{
                if( n_l.length != 11){ // 
                    _this.load = false
                    MessageBox('', '');
                }else{
                    if( p_l > 16 || p_l < 6 ){ // 
                        _this.load = false
                        MessageBox('', '616');
                    }else{ // 
                        register({ // 
                        nick_name:_this.nickname,
                        mobile:_this.mobile,
                        username:_this.mobile,
                        password:_this.password
                        }).then((response)=>{
                            cookie.setCookie('username',_this.username,1)
                            cookie.setCookie('nickname',response.data.nick_name,1)
                            cookie.setCookie('token',response.data.token,1)
                            _this.$store.dispatch('setInfo') // store
                            getUserDetail().then((datas) =>{
                                // console.log(datas.data)
                                cookie.setCookie('id',datas.data.id,1)
                                cookie.setCookie('avatar',datas.data.avatar,1)
                                _this.$store.dispatch('setInfo'); //
                                _this.load = false
                                _this.$router.push({ name: 'home'})
                            }).catch(function(err){ // console.log(err)
                                _this.load = false
                                _this.$router.push({ name: 'login'})
                            })
                        }).catch(function (error) {
                            if( 'username' in error ){
                                _this.load = false
                                MessageBox('', '');
                            }else if( 'nick_name' in error){
                                _this.load = false
                                MessageBox('', '');
                            }else{
                                _this.load = false
                                MessageBox('', '');
                            }
                        })

                    }
                }
            }
        },
    },
    mounted(){
        this.load = false
    },
    created(){
        //
        cookie.delCookie('token')
        cookie.delCookie('username')
        cookie.delCookie('id')
        cookie.delCookie('nickname')
        cookie.delCookie('avatar')
        //store
        this.$store.dispatch('setInfo')
    },
    watch:{
        selected:function(){
            let _this = this
            _this.$router.push({
                name:_this.selected
            })
        }
    }
}
</script>
